import { useState, useEffect } from 'react';
import { Controller, useForm } from 'react-hook-form';

import Form from "../ui/form/Form";
import ReactSelect from '../ui/select/ReactSelect';
import ReactDataPicker from '../ui/dataPicker/DataPicker';
import TextAria from '../ui/textAria/TextAria';
import Button from '../ui/button/Button';

import { towerOptions, floorOptions, roomOptions } from '../../utils/fieldsOptions'

import style from './booking-form.module.css';

const BookingForm = (props) => {
    const { handleSubmit, reset, control } = useForm();
    const [date, setDate] = useState();

    useEffect(() => {
        setDate(new Date())
      },
      []);

    //  
    const onReset = () => {
        reset({
            towel: '',
            floor: '',
            meetingRoom: '',
            date: '',
            comments: ''
        })
    }

    //  
    const onSubmit = (data) => {
        let promise = new Promise((resolve, reject) => {
            resolve();
        });

        promise.then(res => {
            onReset();

            console.log(JSON.stringify(data))

            props.setModal({
                visible: true,
                content: '  '
            })
        }).catch(e => {
            props.setModal({
                visible: true,
                content: '    .    '
            })
        })

    };

    return (
        <div className={style.container}>
            <h2 className={style.formName}>
                  
            </h2>
            <Form handleSubmit={handleSubmit} reset={reset} onSubmit={onSubmit}>
                <Controller
                    control={ control }
                    name='towel'
                    rules={{
                    required: ' '
                    }}
                    render={({field: { onChange, value }, fieldState: { error }} ) => (
                        <ReactSelect 
                            className={"react-select-container"}
                            classNamePrefix={"react-select"}
                            placeholder={" *"}
                            options={towerOptions}
                            onChange={onChange}
                            value={value}
                            error={error}
                            errorMessage={"  "}
                        />
                    )}
                />

                <Controller
                    control={control}
                    name='floor'
                    rules={{
                    required: ' '
                    }}
                    render={({field: { onChange, value }, fieldState: { error }} ) => (
                        <ReactSelect 
                            className={"react-select-container"}
                            classNamePrefix={"react-select"}
                            placeholder={" *"}
                            options={floorOptions}
                            onChange={ onChange }
                            value={value}
                            error={error}
                            errorMessage={"  "}
                        />
                    )}
                />

                <Controller
                    control={control}
                    name='meetingRoom'
                    rules={{
                    required: ' '
                    }}
                    render={({field: { onChange, value }, fieldState: { error }} ) => (
                        <ReactSelect 
                            className={"react-select-container"}
                            classNamePrefix={"react-select"}
                            placeholder={" *"}
                            options={roomOptions}
                            onChange={ onChange }
                            value={value}
                            error={error}
                            errorMessage={"  "}
                        />
                    )}
                />

                <Controller
                    control={control}
                    name='date'
                    rules={{
                    required: '   '
                    }}
                    render={({field: { onChange, value }, fieldState: { error }} ) => (
                        <ReactDataPicker 
                            onChange={ onChange }
                            value={ value }
                            showTimeSelect={ true }
                            dateFormat="dd/MM/yyyy HH:mm"
                            locale="ru_RU"
                            minDate={ date }
                            placeholderText="   *"
                            timeCaption=""
                            error={ error }
                            errorMessage={ "  " }
                        />
                    )}
                />

                <Controller
                    control={control}
                    name='comments'
                    render={({field: { onChange, value, name }} ) => (
                        <TextAria
                            onChange={ onChange }
                            name={name}
                            value={ value }
                            placeholder=""
                        />
                    )}
                />

                <div className={style.buttons}>
                    <Button type="submit">
                        
                    </Button>

                    <Button 
                        type="button"
                        onClick={() => onReset()}
                    >
                         
                    </Button>
                </div>
            </Form>
        </div>
    )
}

export default BookingForm