const towerOptions = [{
    value: "",
    label: " ",
  },{
    value: "",
    label: " ",
  }]

const floorOptions = []

const roomOptions = [];

//        
const fillOptions = (start, end, arr, label) => {
  for(let i = start; i <= end; i++) {
    arr.push({
      value: `${i}`,
      label: `${label} ${i}`,
    })
  }
}

fillOptions(3, 27, floorOptions, "");
fillOptions(1, 10, roomOptions, " ");

export { towerOptions, floorOptions, roomOptions }